.replantedTrees {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 50px 20px;
    background: #e3ffea63; 
    text-align: center;
    justify-content: center;
  }
  
  .treeImage {
    width: 40%;
    max-width: 400px;
    border-radius: 8px;
    margin: 10px;
  }
  
  @media screen and (max-width: 800px){
    .replantedTrees {
      flex-direction: column;
    }
  
    .treeImage {
      width: 90%;
      max-width: 400px;
      border-radius: 8px;
      margin: 10px;
    }
  }
  