/* DonationForm.css */

.donation-form-container {
    max-width: 400px;
    margin: auto;
    padding: 20px;
    background-color: #f8f8f8;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    margin-top: 120px;
  }
  
  h2 {
    text-align: center;
    color: #333;
  }
  
  form {
    display: flex;
    flex-direction: column;
  }
  
  label {
    margin-bottom: 8px;
    color: #333;
  }
  
  input {
    padding: 10px;
    margin-bottom: 16px;
    border: 1px solid #ccc;
    border-radius: 10px;
  }

  .link-input {
    margin-top: 10px;
    border-radius: 10px;
  }
  
  button {
    background-color: #69c46d;
    color: #fff;
    padding: 12px;
    border: none;
    border-radius: 100px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: bold;
  }
  
  button:hover {
    background-color: #6eac71;
  }
  